import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Grow, Typography, Grid, Button } from "@material-ui/core";
import { ArrowBackTwoTone } from "@material-ui/icons";
import IconButton from "@material-ui/icons/KeyboardReturn";
import { styles } from "../../styles";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import { URL } from "../../BaseUrl";
import axios from "axios";
import { Table } from "../../components/Table";
import {
  tools_status_module_columns,
  tools_type_module_columns,
  visit_List_type_module_columns,
  Fund_List_type_module_columns,
  Contact_List_type_module_columns
} from "../../server/columns";
import FullScreenModal from '../VisitDetailForm/FullScreenModal';
import TextField from '@material-ui/core/TextField';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: "100%",
  },
}));

function Tools(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = useState(0);
  const [Status, setStatus] = useState([]);
  const [projectType, setProjectType] = useState([]);
  const [userTypes, setUserTypes] = useState([]);
  const [taskStatus, setTaskStatus] = useState([]);
  const [visitType, setVisitType] = useState([]);
  const [contractType, setContractType] = useState([]);
  const [fundType, setFundType] = useState([]);

  const [statusLoading, setStatusLoading] = useState(false);
  const [typeLoading, setTypeLoading] = useState(false);
  const [userLoading, setUserLoading] = useState(false);
  const [taskLoading, setTaskLoading] = useState(false);
  const [visitLoading, setVisitLoading] = useState(false);
  const [contractLoading, setContractLoading] = useState(false);
  const [fundTypeLoading, setFundTypeLoading] = useState(false);

  // All FullScreen Dialog Code
  const [dialogState, setDialogState] = useState(false);
  const [rowData, setRowData] = useState(null);
  const [VisitName, setVisitName] = useState("");
  const [contractName, setContractName] = useState("");
  const [fundName, setFundName] = useState("");
  const [dialogStateFundType, setDialogStateFundType] = useState(false);
  const [dialogStateContract, setDialogStateContract] = useState(false);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  const GetStatus = async () => {
    setStatusLoading(true);
    let res = await axios.post(URL + "/api/GetProjectStatus");
    if (res.data) {
      setStatus(res.data);
      setStatusLoading(false);
    }
  };

  const GetProjectTypes = async () => {
    let res = await axios.post(URL + "/api/GetProjectTypes");
    if (res.data) {
      setProjectType(res.data);
      setTypeLoading(false);
    }
  };

  const GetUsersTypes = async () => {
    let res = await axios.post(URL + "/api/GetAllUserType");
    if (res.data) {
      setUserTypes(res.data);
      setUserLoading(false);
    }
  };

  const GetTaskStatus = async () => {
    let res = await axios.post(URL + "/Api/GetStatus");
    if (res.data) {
      setTaskStatus(res.data);
      setTaskLoading(false);
    }
  };

  const GetVisitList = async () => {
    let res = await axios.post(URL + "/Api/GetClientVisitType");
    if (res.data) {
      setVisitType(res.data);
      setVisitLoading(false);
    }
  };

  const GetContractList = async () => {
    let res = await axios.post(URL + "/api/ContractType");
    if (res.data) {
      console.log("ContractTypes => ", res.data);
      setContractType(res.data);
      setContractLoading(false);
    }
  };

  const GetFundList = async () => {
    let res = await axios.post(URL + "/api/GetFundTypes");
    if (res.data) {
      console.log("FundTypes => ", res.data);
      setFundType(res.data);
      setFundTypeLoading(false);
    }
  };

  useEffect(() => {
    GetContractList();
    GetFundList();
    GetStatus();
    GetProjectTypes();
    GetUsersTypes();
    GetTaskStatus();
    GetVisitList();
  }, []);

  function reloadContentContractList() {
    setContractLoading(true);
    setContractType([]);
    GetContractList();
  }

  function reloadContentFundList() {
    setFundTypeLoading(true);
    setFundType([]);
    GetFundList();
  }

  function reloadContentVisitList() {
    setVisitLoading(true);
    setVisitType([]);
    GetVisitList();
  }

  function reloadContentProjectStatus() {
    setStatusLoading(true);
    setStatus([]);
    GetStatus();
  }
  function reloadContentProjectType() {
    setTypeLoading(true);
    setProjectType([]);
    GetProjectTypes();
  }
  function reloadContentUserType() {
    setUserLoading(true);
    setUserTypes([]);
    GetUsersTypes();
  }

  function reloadContentTaskType() {
    setTaskLoading(true);
    setTaskStatus([]);
    GetTaskStatus();
  }



  const openDailogState = (data=null) => {
    setRowData(data)
    setVisitName(data !== null ? data.Title :"")
    setDialogState(true);
  }

  const closeDailogState = () => {
    setDialogState(false)
    setVisitName("");
    setRowData(null)
  }

  // Dialog State for FundTye

  const openDailogStateFundType = (data = null) => {
    setRowData(data)
    setFundName(data !== null ? data.FundType : "")
    setDialogStateFundType(true);
  }

  const closeDailogStateFundType = () => {
    setDialogStateFundType(false)
    setFundName("");
    setRowData(null)
  }

  // Dialog State for Contract type

  const openDailogStateContractType = (data = null) => {
    setRowData(data)
    setContractName(data !== null ? data.ContractType : "")
    setDialogStateContract(true);
  }

  const closeDailogStateContractType = () => {
    setDialogStateContract(false)
    setContractName("");
    setRowData(null)
  }


  const onModalVisitSubmit = () => {
    axios.post(URL + "/api/SaveClientVisitType" ,{
      nIsNew: rowData === null? true : false,
      nCurrentID: rowData === null ? -1 :rowData.ID,
      nTitle: VisitName
    }).then((response) => {
      if (response.data.result) {
        reloadContentVisitList();
        setVisitName("");
        setDialogState(false);
        setRowData(null);
      }
    })
  }

  const onModalFundSubmit = () => {
    axios.post(URL + "/api/SaveFundType", {
      IsNew: rowData === null ? true : false,
      CurrentId: rowData === null ? -1 : rowData.ID,
      Name: fundName
    }).then((response) => {
        reloadContentFundList();
        setFundName("");
        setDialogStateFundType(false);
        setRowData(null); 
    })
  }

  const onModalContractSubmit = () => {
    axios.post(URL + "/api/SaveContractType", {
      IsNew: rowData === null ? true : false,
      CurrentId: rowData === null ? -1 : rowData.ID,
      Name: contractName
    }).then((response) => {
      reloadContentContractList();
      setContractName("");
      setDialogStateContract(false);
      setRowData(null);
    })
  }

  return (
    <Grow in={true}>
      <div>
        {/* <Typography
          variant="h6"
          gutterBottom
          style={{ ...styles.alignItems_center, ...styles.d_flex }}
        > */}
        {/* <Button
            variant="contained"
            style={{
              background: "#2f3b7d",
              color: "#fff",
              marginRight: "10px",
            }}
            onClick={() => props.history.goBack()}
          > */}
        <ArrowBackTwoTone
          onClick={() => props.history.goBack()}
          style={{ marginRight: "7px" }}
        />
        {/* </Button>
        </Typography> */}
        <div className={{ margin: "7px" }}>
          <Typography variant="h6" gutterBottom>
            Tools
          </Typography>
        </div>

        <div className={classes.root}>
          <AppBar position="static" color="primary">
            <Tabs
              value={value}
              onChange={handleChange}
              indicatorColor="primary"
              textColor="inherit"
              variant="fullWidth"
              aria-label="full width tabs example"
            >
              <Tab label="Project Status" {...a11yProps(0)} />
              <Tab label="Project Type" {...a11yProps(1)} />
              {/* <Tab label="User Type" {...a11yProps(2)} /> */}
              <Tab label="Task Type" {...a11yProps(2)} />
              <Tab label="Visit Status" {...a11yProps(3)} />
              <Tab label="Fund Type" {...a11yProps(4)} />
              <Tab label="Contract Type" {...a11yProps(4)} />
            </Tabs>
          </AppBar>
          <SwipeableViews
            axis={theme.direction === "rtl" ? "x-reverse" : "x"}
            index={value}
            onChangeIndex={handleChangeIndex}
          >
            <TabPanel value={value} index={0} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="Project Status"
                    columns={tools_status_module_columns}
                    data={Status}
                    isLoading={statusLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentProjectStatus}
                    page={0}
                    totalRecords={Status.length}
                    onRefresh={reloadContentProjectStatus}
                    // nullSelected={nullSelected}
                    variant={62}
                    size={Status.length}
                  />
                </Grid>
              </Grid>
            </TabPanel>
            <TabPanel value={value} index={1} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="Project Type"
                    columns={tools_type_module_columns}
                    data={projectType}
                    isLoading={typeLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentProjectType}
                    page={0}
                    totalRecords={projectType.length}
                    onRefresh={reloadContentProjectType}
                    // nullSelected={nullSelected}
                    variant={63}
                    size={projectType.length}
                  />
                </Grid>
              </Grid>
            </TabPanel>
            {/* <TabPanel value={value} index={2} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="User Type"
                    columns={tools_type_module_columns}
                    data={userTypes}
                    isLoading={userLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentUserType}
                    page={0}
                    totalRecords={userTypes.length}
                    onRefresh={reloadContentUserType}
                    // nullSelected={nullSelected}
                    variant={64}
                    size={userTypes.length}
                  />
                </Grid>
              </Grid>
            </TabPanel> */}
            <TabPanel value={value} index={2} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="taskType"
                    columns={tools_type_module_columns}
                    data={taskStatus}
                    isLoading={taskLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentTaskType}
                    page={0}
                    totalRecords={taskStatus.length}
                    onRefresh={reloadContentTaskType}
                    // nullSelected={nullSelected}
                    variant={65}
                    size={taskStatus.length}
                  />
                </Grid>
              </Grid>
            </TabPanel>
            <TabPanel value={value} index={3} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="Visit Type"
                    columns={visit_List_type_module_columns}
                    data={visitType}
                    isLoading={visitLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentVisitList}
                    page={0}
                    totalRecords={visitType.length}
                    onRefresh={reloadContentVisitList}
                    // nullSelected={nullSelected}
                    variant={68}
                    size={visitType.length}
                    openVisitTypeEdit={openDailogState}
                  />
                </Grid>
              </Grid>
            </TabPanel>
            
            {/* Fund Type */}

            <TabPanel value={value} index={4} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="Fund Type"
                    columns={Fund_List_type_module_columns}
                    data={fundType}
                    isLoading={fundTypeLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentFundList}
                    page={0}
                    totalRecords={fundType.length}
                    onRefresh={reloadContentFundList}
                    // nullSelected={nullSelected}
                    variant={70}
                    size={fundType.length}
                    openFundTypeEdit={openDailogStateFundType}
                  />
                </Grid>
              </Grid>
            </TabPanel>


            {/* Contract Type */}

            <TabPanel value={value} index={5} dir={theme.direction}>
              <Grid container>
                <Grid item xs={12}>
                  <Table
                    title="Contract Type"
                    columns={Contact_List_type_module_columns}
                    data={contractType}
                    isLoading={contractLoading}
                    // error={error}
                    // onChangePage={changePageArchitect}
                    onSuccess={reloadContentContractList}
                    page={0}
                    totalRecords={contractType.length}
                    onRefresh={reloadContentContractList}
                    // nullSelected={nullSelected}
                    variant={71}
                    size={contractType.length}
                    openContractTypeEdit={openDailogStateContractType}
                  />
                </Grid>
              </Grid>
            </TabPanel>

          </SwipeableViews>
        </div>
        <FullScreenModal onSubmit={onModalVisitSubmit} isOpen={dialogState} appBarTitle="Visit Type" buttonTitle="Save" handleClose={closeDailogState}>
          <div style={{
            padding: 20
          }}>
            <TextField id="outlined-basic" label="Name" value={VisitName} required onChange={(e)=>setVisitName(e.target.value)} />
            </div>
        </FullScreenModal>
        
        {/* Fund Type */}

        <FullScreenModal onSubmit={onModalFundSubmit} isOpen={dialogStateFundType} appBarTitle="Fund Type" buttonTitle="Save" handleClose={closeDailogStateFundType}>
          <div style={{
            padding: 20
          }}>
            <TextField id="outlined-basic" label="Name" value={fundName} required onChange={(e) => setFundName(e.target.value)} />
          </div>
        </FullScreenModal>


        {/* Contract Type */}

        <FullScreenModal onSubmit={onModalContractSubmit} isOpen={dialogStateContract} appBarTitle="Contract Type" buttonTitle="Save" handleClose={closeDailogStateContractType}>
          <div style={{
            padding: 20
          }}>
            <TextField id="outlined-basic" label="Name" value={contractName} required onChange={(e) => setContractName(e.target.value)} />
          </div>
        </FullScreenModal>
        
      </div>
    </Grow>
  );
}

export default withRouter(Tools);
